import React, { useEffect } from "react";
import { useHistory } from "react-router-dom";
import "./StartPage.css";
import profile from "../img/cvs.png";
import grid1 from "../img/grid1.jpg";
import grid2 from "../img/grid2.png";
import grid3 from "../img/grid3.jpg";
import grid4 from "../img/grid4.jpg";
import grid5 from "../img/grid5.jpg";
import grid6 from "../img/grid6.jpg";
import grid7 from "../img/grid7.jpg";
import grid8 from "../img/grid8.jpg";
import grid9 from "../img/grid9.jpg";
import grid10 from "../img/grid10.jpg";
import hamburg from "../img/hambur.png";
import diploma from "../img/diploma.png";
import Fade from "react-reveal/Fade";


const StartPage = () => {
  const { push } = useHistory();
  const history = useHistory();


  //npm run deploy -- -m "Deploy React app to GitHub Pages"

  window.onscroll = function () {
    scrollFunction();
  };

  function linkClick() {
    let width = window.innerWidth;
    if (width < 769) {
      var li = document.getElementById("link");
      li.style.width = "28%";
      var lis = document.getElementById("links");
      lis.style.display = "block";
    } else {
      if (document.getElementById("link") != "null") {
        var li = document.getElementById("link");
        console.log(li.style.width);
        if (li.style.width === "90%") {
          li.style.width = "0.3vw";
          li.style.height = "1vw";
          var lis = document.getElementById("links");
          lis.style.display = "none";
        } else if (li.style.width === "0.3vw") {
          li.style.width = "90%";
          var lis = document.getElementById("links");
          lis.style.display = "block";
        }
      }
    }
  }

  function scrollFunction() {
    let width = window.innerWidth;
    if (width < 769) {
      var li = document.getElementById("link");
      li.style.width = "45%";
      li.style.height = "3vw";
      var lis = document.getElementById("links");
      lis.style.display = "block";
    } else {
      if (
        document.body.scrollTop > 50 ||
        document.documentElement.scrollTop > 50
      ) {
        var li = document.getElementById("link");
        li.style.width = "0.3vw";

        li.style.height = "1vw";
        var lis = document.getElementById("links");
        lis.style.display = "none";
        li.onclick = linkClick;
      } else {
        var li = document.getElementById("link");
        li.style.width = "90%";
        var lis = document.getElementById("links");
        lis.style.display = "block";
        li.onclick = linkClick;
      }
    }
  }

  function handleClick() {
    history.push("/aliceljungholm/Project");
  }
  function handleClickMasterThesis() {
    history.push("/aliceljungholm/master-thesis");
  }
  function handleHomeClick() {
    history.push("/aliceljungholm");
  }

  return (
    <div className="main">
      <div className="content">
        <div id="link">
          <img id="linkimg" alt="" source={hamburg}></img>
          <Fade left>
            <div id="links">
              <a id="menuLink" onClick="#content">Home</a> |{" "}
              <a id="menuLink" onClick={handleClick}>Projects</a> |{" "}
              <a id="menuLink" href="#education">Education</a> |{" "}
              <a id="menuLink" href="#contact-me">Contact</a>
              
              
            </div>
          </Fade>
        </div>

        <div className="header">
          <div className="fadeText">
            <Fade left>
              <div className="mainH1">
                <h1>
                  <b>Hi my name is Alice,</b>
                </h1>

                <p> and I am a M.Sc. Graduate in Interaction Technology and Design</p>
              </div>
            </Fade>
          </div>
        </div>
        {/* /**Kanske tar en hoover med information här istället??? */}
      </div>
      <div className="comp">
        <Fade bottom>
          <div>
            <h1 id="bounceH1">Projects</h1>
          </div>
        </Fade>

        <Fade bottom>
          <div className="proj">
            <p>
              Projects done recently, some done by myself, some with an
              amazing team.
            </p>
            <button onClick={handleClick}>See more!</button>
          </div>
        </Fade>
      </div>

      <div id="divide"></div>
      <div className="aboutMe">
        <Fade left>
          <h1 id="bounceH1">About ME</h1>
        </Fade>
        <div id="aboutcontent">
          <Fade left>
            <ul>
              <li>I was born in the late 90's in the northern parts of Sweden</li>
              <li>I am a M.Sc. Graduate in Interaction Technology and Design from Umeå University, Sweden </li>
              <li>I am a logical thinker with a lot of creative energy. Best of both worlds I would say</li>
              <li>I have experience in multiple coding languagues but prefer Java, Python, HTML, CSS, and JavaScript</li>
              <li>Fluent in Figma and Adobe XD</li>
              <li>Familiar with SQL databases, AI, and ML algorithm's</li>
            </ul>
          </Fade>

          <img alt="Picture of the creator of this page" src={grid8}></img>
        </div>
      </div>

      <div id="education"></div>

      <div id="educ">
      <Fade left>
        <h1 id="bounceH1">education</h1>
      </Fade>
      <Fade left>
        <div id="educontent">
          <p>
            Finished my Master in Engineering in Interaction
            Technology and Design at Umeå Univeristy, Sweden. My education was
            primarily focused on services in mobile systems. Where I became an
            expert in the interaction between man and machine and creating technology
            that works, is durable, and easy and fun to use. <br>
            </br>
            The details about my Master Thesis can be found <a onClick={handleClickMasterThesis}>here</a>
          </p>
          <p> </p>
         
          <img alt="decorative picture" src={diploma}></img>
        </div>
        </Fade>
      </div>
      <div id="contact-me"></div>
      <div id="contact">
        <Fade>
        <h1 id="bounceH1">contact</h1>
        </Fade>
        
        <div id="contactcontent">
        <Fade>
          <p>
            <b>Contact via email:</b>
            <br />
            aliceljungholm@gmail.com.
            <br />
            <br />
            <b>or LinkedIn:</b>
            <br />
            <a
              href="https://www.linkedin.com/in/alice-ljungholm-6b780214b"
              target="_blank"
            >
              Alice Ljungholm LinkedIn
            </a>
          </p>
          </Fade>
        </div>
        
      </div>

      <div className="photoGridc1">
        <img alt="" src={grid1}></img>
        <img alt="" src={grid2}></img>
        <img alt="" src={grid3}></img>
        <img alt="" src={grid4}></img>
        <img alt="" src={grid5}></img>
      </div>
      <div className="photoGridc2">
        <img alt="" src={grid6}></img>
        <img alt="" src={grid7}></img>
        <img alt="" src={grid8}></img>
        <img alt="" src={grid9}></img>
        <img alt="" src={grid10}></img>
      </div>
      <div id="FreeLink">       <a 
        href="https://www.flaticon.com/free-icons/education"
        title="education icons"
      >
        Education icons created by Freepik - Flaticon
      </a></div>

    </div>
  );
};
export default StartPage;
